'use client'

import { useEffect, useState } from 'react'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { CheckCircle, Target, Clock } from 'lucide-react'
import confetti from 'canvas-confetti'

interface WinMeterProps {
  mustDoCompleted: number
  focusBlocksCompleted: number
  totalMustDos: number
  totalFocusBlocks: number
}

export function WinMeter({ 
  mustDoCompleted, 
  focusBlocksCompleted, 
  totalMustDos, 
  totalFocusBlocks 
}: WinMeterProps) {
  const [isAnimating, setIsAnimating] = useState(false)
  const [previousMustDos, setPreviousMustDos] = useState(mustDoCompleted)
  const [previousFocusBlocks, setPreviousFocusBlocks] = useState(focusBlocksCompleted)

  const isGreenDay = mustDoCompleted >= 3 && focusBlocksCompleted >= 2
  const mustDoProgress = (mustDoCompleted / 3) * 100
  const focusBlockProgress = (focusBlocksCompleted / 2) * 100
  const overallProgress = ((mustDoCompleted + focusBlocksCompleted) / 5) * 100

  // Trigger confetti when reaching green day
  useEffect(() => {
    if (isGreenDay && (previousMustDos < 3 || previousFocusBlocks < 2)) {
      triggerConfetti()
    }
    setPreviousMustDos(mustDoCompleted)
    setPreviousFocusBlocks(focusBlocksCompleted)
  }, [mustDoCompleted, focusBlocksCompleted, isGreenDay, previousMustDos, previousFocusBlocks])

  const triggerConfetti = () => {
    setIsAnimating(true)
    
    // Multiple confetti bursts
    const duration = 3000
    const animationEnd = Date.now() + duration

    const randomInRange = (min: number, max: number) => {
      return Math.random() * (max - min) + min
    }

    const interval = setInterval(() => {
      const timeLeft = animationEnd - Date.now()

      if (timeLeft <= 0) {
        clearInterval(interval)
        setIsAnimating(false)
        return
      }

      // Random confetti bursts
      confetti({
        particleCount: 50,
        startVelocity: 30,
        spread: 360,
        origin: { x: randomInRange(0.1, 0.3), y: Math.random() - 0.2 }
      })

      confetti({
        particleCount: 50,
        startVelocity: 30,
        spread: 360,
        origin: { x: randomInRange(0.7, 0.9), y: Math.random() - 0.2 }
      })
    }, 250)
  }

  const getProgressColor = (progress: number) => {
    if (progress >= 100) return 'bg-green-500'
    if (progress >= 66) return 'bg-yellow-500'
    if (progress >= 33) return 'bg-orange-500'
    return 'bg-red-500'
  }

  const getStatusText = () => {
    if (isGreenDay) return 'ðŸŽ‰ Green Day!'
    if (mustDoCompleted >= 3) return 'Must-Dos Complete!'
    if (focusBlocksCompleted >= 2) return 'Focus Blocks Complete!'
    return 'Keep Going!'
  }

  const getStatusColor = () => {
    if (isGreenDay) return 'text-green-600'
    if (mustDoCompleted >= 3 || focusBlocksCompleted >= 2) return 'text-blue-600'
    return 'text-gray-600'
  }

  return (
    <Card className={`transition-all duration-500 ${isAnimating ? 'scale-105 shadow-lg' : ''}`}>
      <CardContent className="p-6">
        <div className="text-center">
          <div className={`text-3xl font-bold mb-2 ${getStatusColor()}`}>
            {getStatusText()}
          </div>
          
          <div className="text-4xl font-bold text-gray-900 mb-4">
            {mustDoCompleted + focusBlocksCompleted}/5
          </div>

          {/* Overall Progress Bar */}
          <div className="mb-6">
            <div className="w-full bg-gray-200 rounded-full h-3 mb-2">
              <div 
                className={`h-3 rounded-full transition-all duration-500 ${getProgressColor(overallProgress)}`}
                style={{ width: `${Math.min(overallProgress, 100)}%` }}
              />
            </div>
            <div className="text-sm text-gray-600">
              {Math.round(overallProgress)}% Complete
            </div>
          </div>

          {/* Breakdown */}
          <div className="grid grid-cols-2 gap-4">
            <div className="text-center">
              <div className="flex items-center justify-center gap-2 mb-2">
                <CheckCircle className="h-4 w-4 text-green-600" />
                <span className="text-sm font-medium">Must-Dos</span>
              </div>
              <div className="text-2xl font-bold text-gray-900">
                {mustDoCompleted}/3
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2 mt-2">
                <div 
                  className={`h-2 rounded-full transition-all duration-500 ${getProgressColor(mustDoProgress)}`}
                  style={{ width: `${Math.min(mustDoProgress, 100)}%` }}
                />
              </div>
            </div>

            <div className="text-center">
              <div className="flex items-center justify-center gap-2 mb-2">
                <Clock className="h-4 w-4 text-blue-600" />
                <span className="text-sm font-medium">Focus Blocks</span>
              </div>
              <div className="text-2xl font-bold text-gray-900">
                {focusBlocksCompleted}/2
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2 mt-2">
                <div 
                  className={`h-2 rounded-full transition-all duration-500 ${getProgressColor(focusBlockProgress)}`}
                  style={{ width: `${Math.min(focusBlockProgress, 100)}%` }}
                />
              </div>
            </div>
          </div>

          {/* Status Badges */}
          <div className="flex justify-center gap-2 mt-4">
            {mustDoCompleted >= 3 && (
              <Badge className="bg-green-100 text-green-800">
                <CheckCircle className="h-3 w-3 mr-1" />
                Must-Dos âœ“
              </Badge>
            )}
            {focusBlocksCompleted >= 2 && (
              <Badge className="bg-blue-100 text-blue-800">
                <Target className="h-3 w-3 mr-1" />
                Focus âœ“
              </Badge>
            )}
            {isGreenDay && (
              <Badge className="bg-yellow-100 text-yellow-800 animate-pulse">
                ðŸŽ‰ Green Day!
              </Badge>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
