'use client'

import { useEffect, useCallback } from 'react'

interface KeyboardShortcutsProps {
  onQuickAdd: () => void
  onToggleMustDo: (taskId: string) => void
  onSchedule: (taskId: string) => void
  onFocusBlock: (taskId: string) => void
  selectedTaskId?: string
  onNavigateToToday?: () => void
  onNavigateToWeek?: () => void
  onNavigateToProjects?: () => void
}

export function useKeyboardShortcuts({
  onQuickAdd,
  onToggleMustDo,
  onSchedule,
  onFocusBlock,
  selectedTaskId,
  onNavigateToToday,
  onNavigateToWeek,
  onNavigateToProjects
}: KeyboardShortcutsProps) {
  
  const handleKeyDown = useCallback((event: KeyboardEvent) => {
    // Don't trigger shortcuts when typing in inputs
    if (event.target instanceof HTMLInputElement || 
        event.target instanceof HTMLTextAreaElement ||
        event.target instanceof HTMLSelectElement) {
      return
    }

    // Check for modifier keys
    const isCtrlOrCmd = event.ctrlKey || event.metaKey
    const isShift = event.shiftKey
    const isAlt = event.altKey

    // Navigation shortcuts
    if (isCtrlOrCmd && !isShift && !isAlt) {
      switch (event.key.toLowerCase()) {
        case '1':
          event.preventDefault()
          onNavigateToToday?.()
          break
        case '2':
          event.preventDefault()
          onNavigateToWeek?.()
          break
        case '3':
          event.preventDefault()
          onNavigateToProjects?.()
          break
      }
    }

    // Action shortcuts (no modifiers)
    if (!isCtrlOrCmd && !isShift && !isAlt) {
      switch (event.key.toLowerCase()) {
        case 'n':
          event.preventDefault()
          onQuickAdd()
          break
        case 'm':
          event.preventDefault()
          if (selectedTaskId) {
            onToggleMustDo(selectedTaskId)
          }
          break
        case 's':
          event.preventDefault()
          if (selectedTaskId) {
            onSchedule(selectedTaskId)
          }
          break
        case 'f':
          event.preventDefault()
          if (selectedTaskId) {
            onFocusBlock(selectedTaskId)
          }
          break
      }
    }
  }, [
    onQuickAdd,
    onToggleMustDo,
    onSchedule,
    onFocusBlock,
    selectedTaskId,
    onNavigateToToday,
    onNavigateToWeek,
    onNavigateToProjects
  ])

  useEffect(() => {
    document.addEventListener('keydown', handleKeyDown)
    return () => document.removeEventListener('keydown', handleKeyDown)
  }, [handleKeyDown])
}
